/**
 * Body
 */

body {
	margin: 0;
	padding: 0;
	border: none;
}

.inner {
	position: absolute;
	height: 100%;
	width: 100%;
	margin: 0;
}

/**
 * Container
 */
.Container {
	background-repeat: no-repeat;
	background-size: cover;
	overflow: auto;
	background-image: url(../images/supermarket.jpg);
}

	@media (max-width: 256px) {
		.Container {
			background-image: url(../images/supermarket_256.jpg)
		}
	}

	@media (max-width: 512px) {
		.Container {
			background-image: url(../images/supermarket_512.jpg)
		}
	}

	@media (max-width: 1024px) {
		.Container {
			background-image: url(../images/supermarket_1024.jpg)
		}
	}

	@media (max-width: 2048px) {
		.Container {
			background-image: url(../images/supermarket_1024.jpg)
		}
	}

	.Container .Container__hero {
		height: 100%;
		color: #fff;
		display: table;
		width: 100%;
		text-align: center;
		position: absolute;
		padding: 20px 0 20px 0;
	}

	.Container .Container__center-box {
		display: table-cell;
		vertical-align: middle;
		margin-top: 0;
		margin-bottom: 0;
		float: none;
		z-index: 60;
	}

	.Container .Container__fluid {
		position: relative;
	}

