/**
 * Buttons
 */
.btn-red {
	color: #fff;
	background-color: #EC1D25;
}

.btn-red:hover {
	background-color: #af080b;
}

.btn-link {
	color: #EC1D25;
	font-weight: bold;
}

.btn-link:hover {
	color: #af080b;
	text-decoration: none;
}

/**
 * Inputs
